class MyCarousel extends Component {
    static get CONTAINER_STYLE() {
      return {
        position: "relative",
    height: "30vh",
        width: "100%",
        display: "flex",
        flex: 1,
        justifyContent: "center",
        alignItems: "middle"
        
      };
    }
  
    static get CARD_STYLE() {
      return {
        height: "300px",
        width: "400px",
      
        textAlign: "center",
        background: "#000000",
        color: "#FFF",
        fontFamily: "sans-serif",
        fontSize: "12px",
        textTransform: "uppercase",
        borderRadius: "10px",
        boxSizing: "border-box"
      };
    }
  
   
  }
